.cus-gotop {
  position: fixed;
  z-index: 89;
  right: 10px;
  bottom: -100px;
  width: 46px;
  height: 46px;
  line-height: 52px;
  font-size: 1.4rem;
  text-align: center;
  color: #FFF;
  border-radius: 50% 50% 0 0;
  cursor: pointer;
  background: lighten($theme, 10%);
  transition: all 0.6s ease-out;

  // mix-blend-mode: exclusion;
  &:hover {
    border-radius: 50%;
  }

  &.show {
    bottom: 0;
  }
}
