@import '../../assets/scss/variable';
.footer {
    background-color: $theme;
    text-align: center;
    font-size: 1.6rem;
    color: #fff;
    padding: 10px 0;
    line-height: 2;
    .footer-tips{
      font-size: 1.4rem;
    }
    /*992 - 1199*/
    @media (max-width: 1199px) {
      .footer-tips{
        display: none;
      }
    }
    @media (max-width: 768px) {
      font-size: 1.2rem;
    }
  }
